.program-table-event {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
  background-color: #fff;
  padding: 8px;
  color: #666666;
  font-size: 0.85rem;
  margin-bottom: 1px;
  box-shadow: 0 0 0 1px #E0E0E0;
  overflow: hidden;
  text-overflow: ellipsis;
  text-decoration: none;
  outline: none;
  transition: background-color 200ms;
}
.program-table-event::after {
  content: '';
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 15px;
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0.01), #fff);
  transition: all 200ms;
}
.program-table-event:hover,
.program-table-event:focus {
  color: #666666;
  z-index: 5;
  max-height: none;
  transition: background-color 200ms;
  box-shadow: 0 0 0 1px #E0E0E0, 0 0 25px -5px rgba(0, 0, 0, 0.35);
}
.program-table-event:hover::after,
.program-table-event:focus::after {
  opacity: 0;
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0.01), #fbfbfb);
  transition: all 200ms;
}
.program-table-event:hover .program-table-event-meta-fav,
.program-table-event:focus .program-table-event-meta-fav {
  opacity: 1;
  transition: opacity 200ms;
}
.program-table-event p {
  margin: 0.7em 0;
}
.program-table-event-red {
  border-left: 3px solid #ad1b34;
}
.program-table-event-orange {
  border-left: 3px solid #e17f38;
}
.program-table-event-blue {
  border-left: 3px solid #49b5e8;
}
.program-table-event-navy {
  border-left: 3px solid #1c6eb0;
}
.program-table-event-old-gold {
  border-left: 3px solid #E7C54F;
}
.program-table-event-green {
  border-left: 3px solid #00733D;
}
.program-table-event-green-light {
  border-left: 3px solid #00d973;
}
.program-table-event-meta {
  display: flex;
  font-size: 0.9rem;
  margin-bottom: 3px;
}
.program-table-event-meta-icon {
  display: inline-block;
  width: 8px;
  height: 8px;
  background-color: #49b5e8;
  margin-top: 2px;
  margin-right: 5px;
  border-radius: 10px;
  vertical-align: baseline;
}
.program-table-event-meta-date {
  color: #888;
}
.program-table-event-meta-fav {
  cursor: pointer;
  opacity: 0;
  display: inline-block;
  width: 16px;
  height: 16px;
  vertical-align: bottom;
  transition: opacity 200ms;
  margin-left: auto;
}
.program-table-event-meta-fav.active {
  opacity: 1;
}
.program-table-event-meta-fav.active svg * {
  fill: #49b5e8;
}
.program-table-event-title {
  color: #2b2a29;
  font-size: 1.1rem;
  margin-bottom: 7px;
}
@media (max-width: 1600px) {
  .program-table-event-title {
    font-size: 1rem;
  }
}
.program-table-event-subtitle {
  color: #2b2a29;
  font-size: 1.05rem;
